import { useLocation, useNavigate } from "react-router-dom";
import { useState } from "react";
import axios from "axios";
import CountUp from "react-countup";
import "./SalaryCheckPage.css";

const SalaryCheckPage = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const userId = location.state?.userId;

  const [year, setYear] = useState(new Date().getFullYear());
  const [month, setMonth] = useState(new Date().getMonth() + 1);

  const [salaryData, setSalaryData] = useState(null);
  const [showModal, setShowModal] = useState(false);

  const handleSalaryCheck = () => {
    axios
      .post("/api/salary", {
        usernumber: userId,
        year: year,
        month: month,
      })
      .then((response) => {
        setSalaryData(response.data);
      })
      .catch((error) => {
        console.error(error);
        alert("월급 계산 실패");
      });
  };

  return (
    <div className="home-container">
      <h2 className="welcome-message">월급 계산기</h2>

      <div className="form-group">
        <label>
          연도:
          <input
            type="number"
            value={year}
            onChange={(e) => setYear(Number(e.target.value))}
            className="input-box"
          />
        </label>
      </div>

      <div className="month-buttons">
        {[...Array(12)].map((_, i) => (
          <button
            key={i + 1}
            className={`month-button ${month === i + 1 ? "active" : ""}`}
            onClick={() => setMonth(i + 1)}
          >
            {i + 1}월
          </button>
        ))}
      </div>

      <div className="button-group">
        <button className="sky-button" onClick={handleSalaryCheck}>
          월급(예상) 확인하기
        </button>
        <button className="logout-button" onClick={() => navigate(-1)}>
          뒤로 가기
        </button>
      </div>

      {salaryData && (
        <div className="salary-result">
          <p>
            총액(세전):{" "}
            <CountUp
              start={0}
              end={salaryData.before_tax}
              duration={2}
              separator=","
            />{" "}
            원
          </p>
          <p>
            총액(세후):{" "}
            <CountUp
              start={0}
              end={salaryData.after_tax}
              duration={2}
              separator=","
            />{" "}
            원
          </p>
          <button
            className="sky-button"
            onClick={() => setShowModal(true)}
            style={{ marginTop: "10px" }}
          >
            상세 보기
          </button>
        </div>
      )}

      {showModal && salaryData && (
        <div className="modal-overlay">
          <div className="modal-content">
            <button className="close-button" onClick={() => setShowModal(false)}>
    ✕
  </button>
            <h3>상세 내역</h3>
            <ul className="salary-detail-list">
              <li>기본급: {salaryData.default_salary.toLocaleString()} 원</li>
              <li>야간 수당: {salaryData.night_salary.toLocaleString()} 원</li>
              <li>주휴 수당: {salaryData.weekly_bonus.toLocaleString()} 원</li>
              <li>휴일 수당: {salaryData.holiday_bonus.toLocaleString()} 원</li>
              <li>총액(세전): {salaryData.before_tax.toLocaleString()} 원</li>
              <li>총액(세후): {salaryData.after_tax.toLocaleString()} 원</li>
            </ul>
            <div className="button-group">
              <button
                className="logout-button"
                onClick={() => setShowModal(false)}
              >
                닫기
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default SalaryCheckPage;
